
body {
  margin: 0;
}
#app {
  color: #2c3e50;
  font-family: Lato, Helvetica, Arial, sans-serif;
  padding: 0 15px;
}
.main-view {
  margin-top: 90px;
}
.env {
  font-size: smaller;
  opacity: 0.5;
  text-align: right;
}
.wip {
  color: #ff6a00;
  font-size: smaller;
  opacity: 0.5;
}
a {
  cursor: pointer;
}
button {
  font-family: Lato, Helvetica, Arial, sans-serif;
}
input:not([type=file]),
textarea {
  font-family: Lato, Helvetica, Arial, sans-serif;
  border: 0;
  border-bottom: 2px solid #4000c0;
  padding: 5px;
}
input:not([type=file])::-webkit-input-placeholder,
  textarea::-webkit-input-placeholder {
    color: #abb2b9;
}
input:not([type=file]):-ms-input-placeholder,
  textarea:-ms-input-placeholder {
    color: #abb2b9;
}
input:not([type=file])::placeholder,
  textarea::placeholder {
    color: #abb2b9;
}



